/*                                                      <STOP-SCROLL>                                                                                                                                                            */
/*                                                      <BURGER>                                                                                                                                                            */
* {
  padding: 0px;
  margin: 0px;
  border: 0px;
}

*, *::before, *::after {
  -moz-box-sizing: inherit;
  -webkit-box-sizing: inherit;
  box-sizing: inherit;
}

:focus, :active {
  outline: none;
}

a:focus, a:active {
  outline: none;
}

aside, nav, footer, header, section {
  display: block;
}

html, body {
  position: relative;
  min-width: 320px;
}

html {
  box-sizing: border-box;
}

body {
  height: 100%;
  overflow-x: hidden;
  line-height: 1;
  font-size: 14px;
  font-family: Arial, Helvetica, sans-serif;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}

input, button, textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a, a:visited {
  text-decoration: none;
  color: inherit;
}

ul, li {
  list-style: none;
  margin: 0;
  padding: 0;
}

img {
  vertical-align: top;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: inherit;
  font-size: inherit;
  margin: 0;
}

ul, ol, li, p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
}

button:focus, input:focus {
  outline: transparent;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  background-color: #F5F5F5;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 1024px) {
  .for-mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.row {
  display: flex;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.header__container {
  max-width: 1220px;
  margin-right: auto;
  margin-left: auto;
}

.header__wrapper {
  margin-top: 50px;
  display: flex;
  justify-content: space-between;
}

.header__logo {
  max-width: 200px;
}

.header__logo img {
  width: 100%;
}

.header__content {
  padding-top: 40px;
  display: flex;
  justify-content: space-between;
  height: 100px;
}

.content__card {
  color: #000000;
}

.content__card .card__title {
  margin-bottom: 11px;
  padding-bottom: 10px;
  font-weight: 700;
  font-size: 19px;
  line-height: 1.21;
  text-transform: uppercase;
  max-width: 300px;
  border-bottom: 2px solid #DB0000;
}

.content__card .card__text {
  font-weight: 400;
  font-size: 12px;
  line-height: 1.25;
  text-transform: uppercase;
}

.content__social {
  display: flex;
}

.content__social .social__wrapper {
  display: flex;
  align-items: center;
  margin-right: 30px;
}

.content__social .social__wrapper:last-child {
  margin-right: 0;
}

.content__social .social__wrapper .social__img {
  width: 100%;
  margin-right: 10px;
}

.content__social .social__wrapper .social__img img {
  max-width: 100%;
}

.content__social .social__wrapper .social__img:hover {
  opacity: 0.4;
}

.content__file .file__wrapper {
  display: flex;
}

.content__file .file__img {
  max-width: 100%;
  margin-right: 21px;
}

.content__file .file__img img {
  width: 100%;
}

.content__file .file__img:hover {
  opacity: 0.4;
}

.content__file .file__text {
  max-width: 100px;
  text-decoration: none;
  font-weight: 400;
  font-size: 15px;
  display: flex;
  align-items: center;
  line-height: 1.2;
  color: #292F36;
}

.content__file .file__text:hover {
  text-decoration: underline;
}

.header__content--wrapper {
  width: 75%;
  margin-right: auto;
  margin-left: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.header__nav {
  display: flex;
  background-color: #DBDBDB;
  margin-top: 30px;
}

.header__nav .nav {
  width: 100%;
}

.header__nav .nav-ul {
  height: 40px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.header__nav .nav-ul li {
  max-width: 150px;
  text-align: center;
}

.header__nav .nav__link {
  font-weight: 400;
  font-size: 18px;
  line-height: 1.22;
  text-transform: uppercase;
  color: #000000;
}

.header__nav .nav__link:focus, .header__nav .nav__link:hover, .header__nav .nav__link:active {
  text-decoration: none;
  color: #BF0000;
  font-weight: 700;
  transition: all 0.2s ease;
}

.header__mobile--nav {
  display: none;
}

@media (max-width: 1140px) {
  .content__card .card__title {
    font-size: 16px;
    max-width: 251px;
  }
  .content__card .card__text {
    font-size: 10px;
  }
  .content__social .social__wrapper {
    margin-right: 10px;
  }
}

@media (max-width: 1060px) {
  .content__card .card__title {
    font-size: 14px;
    max-width: 220px;
  }
  .content__card .card__text {
    font-size: 10px;
  }
  .content__social .social__wrapper {
    margin-right: 8px;
  }
  .header__nav .nav__link {
    font-size: 16px;
  }
}

@media (max-width: 990px) {
  .nav-toggle {
    display: block;
  }
  .content__card {
    display: none;
  }
  .content__social .social__wrapper {
    display: none;
  }
  .content__file {
    display: none;
  }
  .header__nav .nav {
    display: none;
  }
  .header__mobile--nav {
    display: block;
  }
  .header__mobile--nav .mobile__nav {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #DBDBDB;
    width: 100vw;
    z-index: 10;
    position: fixed;
    left: 0;
    top: 0;
    transition: all 0.5s;
    opacity: .95;
  }
  .header__mobile--nav .mobile__nav-ul {
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .header__mobile--nav .mobile__nav__link {
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.22;
    text-transform: uppercase;
    color: #000000;
    margin-top: 20px;
  }
  .header__mobile--nav .mobile__nav__link:focus, .header__mobile--nav .mobile__nav__link:hover, .header__mobile--nav .mobile__nav__link:active {
    text-decoration: none;
    color: #BF0000;
    font-weight: 700;
    transition: all 0.2s ease;
  }
  .header__mobile--nav .mobile__social--wrapper {
    position: fixed;
    left: 0;
    top: 310px;
    z-index: 10;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
    width: 100vw;
    height: 40px;
  }
  .header__mobile--nav .mobile__social--wrapper .social__img {
    margin-right: 10px;
    margin-bottom: 10px;
  }
  .header__mobile--nav .mobile__social--wrapper .social__img:hover {
    opacity: 0.6;
  }
  .header__mobile--nav .mobile__file--wrapper {
    position: fixed;
    left: 0;
    top: 380px;
    z-index: 10;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100vw;
    height: 100px;
  }
  .header__mobile--nav .mobile__file--wrapper .file__img:hover {
    opacity: 0.6;
  }
  .header__mobile--nav .mobile__file--wrapper .file__text {
    margin-top: 10px;
    max-width: 120px;
    text-align: center;
  }
}

.nav-toggle {
  width: 30px;
  padding: 10px 0;
  display: none;
  font-size: 0;
  color: transparent;
  border: 0;
  background: none;
  cursor: pointer;
  position: absolute;
  top: 24px;
  right: 24px;
  z-index: 1;
}

.nav-toggle:focus {
  outline: 0;
}

.nav-toggle__item {
  display: block;
  width: 100%;
  height: 3px;
  background-color: white;
  position: absolute;
  top: 50px;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  transition: background .2s linear;
}

.nav-toggle.active .nav-toggle__item {
  background: none;
}

.nav-toggle.active .nav-toggle__item:before {
  transform-origin: left top;
  transform: rotate(45deg) translate3d(0px, -2px, 0);
}

.nav-toggle.active .nav-toggle__item:after {
  transform-origin: left bottom;
  transform: rotate(-45deg) translate3d(0px, 3px, 0px);
}

.nav-toggle__item:before,
.nav-toggle__item:after {
  content: "";
  width: 100%;
  height: 3px;
  background-color: white;
  position: absolute;
  left: 0;
  z-index: 1;
  transition: transform .2s linear;
}

.nav-toggle__item:before {
  top: -8px;
}

.nav-toggle__item:after {
  bottom: -8px;
}
